import LoaderBase, { IIdentifier } from './LoaderBase';

class FabricWorkaroundLoader extends LoaderBase {
  protected get filename(): string {
    return __filename;
  }

  protected get packageFriendlyName(): string {
    return 'Fabric';
  }

  protected get packageName(): string {
    return 'office-ui-fabric-react';
  }

  protected get shouldExportBasePackage(): boolean {
    return true;
  }

  protected getIdentifier(path: string): IIdentifier {
    switch (path) {
      case 'Breadcrumb':
      case 'components/Breadcrumb/index':

      case 'Button':
      case 'components/Button/index':

      case 'Callout':
      case 'components/Callout/index':
      case 'common/DirectionalHint':

      case 'Checkbox':
      case 'components/Checkbox/index':

      case 'ChoiceGroup':
      case 'components/ChoiceGroup/index':

      case 'ColorPicker':
      case 'components/ColorPicker/index':

      case 'CommandBar':
      case 'components/CommandBar/index':

      case 'ContextualMenu':
      case 'components/ContextualMenu/index':

      case 'DatePicker':
      case 'components/DatePicker/index':

      case 'DetailsList':
      case 'components/DetailsList/index':

      case 'DocumentCard':
      case 'components/DocumentCard/index':

      case 'Dropdown':
      case 'components/Dropdown/index':

      case 'Fabric':
      case 'components/Fabric/index':

      case 'Facepile':
      case 'components/Facepile/index':

      case 'FocusTrapZone':
      case 'components/FocusTrapZone/index':

      case 'FocusZone':
      case 'components/FocusZone/index':

      case 'GroupedList':
      case 'components/GroupedList/index':

      case 'Image':
      case 'components/Image/index':

      case 'Label':
      case 'components/Label/index':

      case 'Layer':
      case 'components/Layer/index':

      case 'Link':
      case 'components/Link/index':

      case 'List':
      case 'components/List/index':

      case 'MessageBar':
      case 'components/MessageBar/index':

      case 'MarqueeSelection':

      case 'Nav':
      case 'components/Nav/index':

      case 'OrgChart':
      case 'components/OrgChart/index':

      case 'Overlay':
      case 'components/Overlay/index':

      case 'Panel':
      case 'components/Panel/index':

      case 'PeoplePicker':
      case 'components/PeoplePicker/index':

      case 'Persona':
      case 'components/Persona/index':

      case 'PersonaCard':
      case 'components/PersonaCard/index':

      case 'Pickers':
      case 'components/pickers/index':

      case 'Pivot':
      case 'components/Pivot/index':

      case 'ProgressIndicator':
      case 'components/ProgressIndicator/index':

      case 'SearchBox':
      case 'components/SearchBox/index':

      case 'Slider':
      case 'components/Slider/index':

      case 'Spinner':
      case 'components/Spinner/index':

      case 'TextField':
      case 'components/TextField/index':

      case 'Toggle':
      case 'components/Toggle/index':

      case 'components/MarqueeSelection/MarqueeSelection':

      case 'components/MarqueeSelection/MarqueeSelection.Props':

      case 'utilities/selection/index':

      case 'utilities/selection/interfaces':

      case 'utilities/selection/Selection':

      case 'utilities/selection/SelectionLayout':

      case 'utilities/selection/SelectionZone':

      case 'Utilities':

      case 'common/BaseComponent':

      case 'utilities/Async/Async':

      case 'utilities/KeyCodes':

      case 'utilities/array':

      case 'utilities/autobind':

      case 'utilities/css':

      case 'utilities/dom':

      case 'utilities/eventGroup/EventGroup':

      case 'utilities/hoist':

      case 'utilities/object':

      case 'utilities/rtl':

      case 'utilities/properties':

        return {};

      case 'Dialog':
      case 'components/Dialog/index':
        return {
          exportMap: {
            'default': 'Dialog'
          }
        };

      // Temporary workarounds to eliminate extra bundling and webpack warnings
      // Fix the underlying issues: VSO#274960
      case 'utilities/decorators/withResponsiveMode':
        return {
          export: 'workaround_withResponsiveMode'
        };

      case 'utilities/decorators/withViewport':
        return {
          export: 'workaround_withViewport'
        };

      case 'utilities/dragdrop/DragDropHelper':
        return {
          export: 'workaround_DragDropHelper'
        };

      case 'components/Callout/Callout.scss':
        return {
          export: 'workaround_CalloutScss'
        };

      default:
        return undefined;
    }
  }
}

export = (new FabricWorkaroundLoader()).getWebpackLoader(); // tslint:disable-line:export-name
